export default "import React from 'react';\r\nimport clsx from 'clsx';\r\nimport Badge from '@material-ui/core/Badge';\r\nimport FormControl from '@material-ui/core/FormControl';\r\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\r\nimport FormLabel from '@material-ui/core/FormLabel';\r\nimport Radio from '@material-ui/core/Radio';\r\nimport RadioGroup from '@material-ui/core/RadioGroup';\r\nimport {makeStyles} from '@material-ui/core/styles';\r\n\r\nconst useStyles = makeStyles((theme) => ({\r\n  formControl: {\r\n    marginBottom: theme.spacing(3),\r\n  },\r\n  row: {\r\n    display: 'flex',\r\n    justifyContent: 'center',\r\n  },\r\n  shapeContainer: {\r\n    margin: theme.spacing(2),\r\n  },\r\n  shape: {\r\n    backgroundColor: theme.palette.primary.main,\r\n    width: 40,\r\n    height: 40,\r\n  },\r\n  shapeCircle: {\r\n    borderRadius: '50%',\r\n  },\r\n}));\r\n\r\nexport default function BadgeOverlap() {\r\n  const classes = useStyles();\r\n  const [overlap, setOverlap] = React.useState('rectangle');\r\n\r\n  function handleChange(event) {\r\n    setOverlap(event.target.value);\r\n  }\r\n\r\n  return (\r\n    <div>\r\n      <div className={classes.row}>\r\n        <FormControl component='fieldset' className={classes.formControl}>\r\n          <FormLabel component='legend'>Overlap</FormLabel>\r\n          <RadioGroup value={overlap} onChange={handleChange}>\r\n            <FormControlLabel\r\n              value='rectangle'\r\n              control={<Radio />}\r\n              label='Rectangle'\r\n            />\r\n            <FormControlLabel\r\n              value='circle'\r\n              control={<Radio />}\r\n              label='Circle'\r\n            />\r\n          </RadioGroup>\r\n        </FormControl>\r\n      </div>\r\n      <div className={classes.row}>\r\n        <div className={classes.shapeContainer}>\r\n          <Badge\r\n            color='secondary'\r\n            overlap={overlap}\r\n            badgeContent=' '\r\n            variant='dot'>\r\n            <div\r\n              className={clsx(\r\n                classes.shape,\r\n                overlap === 'circle' && classes.shapeCircle,\r\n              )}\r\n            />\r\n          </Badge>\r\n        </div>\r\n        <div className={classes.shapeContainer}>\r\n          <Badge color='secondary' overlap={overlap} badgeContent=' '>\r\n            <div\r\n              className={clsx(\r\n                classes.shape,\r\n                overlap === 'circle' && classes.shapeCircle,\r\n              )}\r\n            />\r\n          </Badge>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  );\r\n}\r\n";