export default "import React from 'react';\r\nimport {makeStyles} from '@material-ui/core/styles';\r\nimport Badge from '@material-ui/core/Badge';\r\nimport IconButton from '@material-ui/core/IconButton';\r\nimport MailIcon from '@material-ui/icons/Mail';\r\nimport AppBar from '@material-ui/core/AppBar';\r\nimport Tabs from '@material-ui/core/Tabs';\r\nimport Tab from '@material-ui/core/Tab';\r\nimport Typography from '@material-ui/core/Typography';\r\nimport Button from '@material-ui/core/Button';\r\n\r\nconst useStyles = makeStyles((theme) => ({\r\n  margin: {\r\n    margin: theme.spacing(2),\r\n  },\r\n  padding: {\r\n    padding: theme.spacing(0, 2),\r\n  },\r\n  tabItem: {\r\n    minWidth: 100,\r\n  },\r\n}));\r\n\r\nexport default function SimpleBadge() {\r\n  const classes = useStyles();\r\n\r\n  return (\r\n    <div>\r\n      <div>\r\n        <Badge className={classes.margin} badgeContent={4} color='primary'>\r\n          <MailIcon />\r\n        </Badge>\r\n        <Badge className={classes.margin} badgeContent={10} color='secondary'>\r\n          <MailIcon />\r\n        </Badge>\r\n        <IconButton aria-label='4 pending messages' className={classes.margin}>\r\n          <Badge badgeContent={4} color='primary'>\r\n            <MailIcon />\r\n          </Badge>\r\n        </IconButton>\r\n      </div>\r\n      <AppBar position='static' className={classes.margin}>\r\n        <Tabs value={0}>\r\n          <Tab\r\n            className={classes.tabItem}\r\n            label={\r\n              <Badge\r\n                className={classes.padding}\r\n                color='secondary'\r\n                badgeContent={4}>\r\n                Item One\r\n              </Badge>\r\n            }\r\n          />\r\n          <Tab className={classes.tabItem} label='Item Two' />\r\n          <Tab className={classes.tabItem} label='Item Three' />\r\n        </Tabs>\r\n      </AppBar>\r\n      <Badge color='primary' badgeContent={4} className={classes.margin}>\r\n        <Typography className={classes.padding}>Typography</Typography>\r\n      </Badge>\r\n      <Badge color='primary' badgeContent={4} className={classes.margin}>\r\n        <Button variant='contained'>Button</Button>\r\n      </Badge>\r\n    </div>\r\n  );\r\n}\r\n";