export default "import React from 'react';\r\nimport Badge from '@material-ui/core/Badge';\r\nimport FormControl from '@material-ui/core/FormControl';\r\nimport FormControlLabel from '@material-ui/core/FormControlLabel';\r\nimport FormLabel from '@material-ui/core/FormLabel';\r\nimport Radio from '@material-ui/core/Radio';\r\nimport RadioGroup from '@material-ui/core/RadioGroup';\r\nimport {makeStyles} from '@material-ui/core/styles';\r\nimport MailIcon from '@material-ui/icons/Mail';\r\n\r\nconst useStyles = makeStyles((theme) => ({\r\n  root: {\r\n    display: 'flex',\r\n    flexDirection: 'column',\r\n  },\r\n  formControl: {\r\n    margin: theme.spacing(3),\r\n  },\r\n  row: {\r\n    display: 'flex',\r\n    justifyContent: 'center',\r\n  },\r\n  margin: {\r\n    margin: theme.spacing(2),\r\n  },\r\n}));\r\n\r\nexport default function BadgeAlignment() {\r\n  const classes = useStyles();\r\n  const [horizontal, setHorizontal] = React.useState('right');\r\n  const [vertical, setVertical] = React.useState('top');\r\n\r\n  function handleHorizontalChange(event) {\r\n    setHorizontal(event.target.value);\r\n  }\r\n\r\n  function handleVerticalChange(event) {\r\n    setVertical(event.target.value);\r\n  }\r\n\r\n  return (\r\n    <div className={classes.root}>\r\n      <div className={classes.row}>\r\n        <FormControl component='fieldset' className={classes.formControl}>\r\n          <FormLabel component='legend'>Vertical</FormLabel>\r\n          <RadioGroup value={vertical} onChange={handleVerticalChange}>\r\n            <FormControlLabel value='top' control={<Radio />} label='Top' />\r\n            <FormControlLabel\r\n              value='bottom'\r\n              control={<Radio />}\r\n              label='Bottom'\r\n            />\r\n          </RadioGroup>\r\n        </FormControl>\r\n        <FormControl component='fieldset' className={classes.formControl}>\r\n          <FormLabel component='legend'>Horizontal</FormLabel>\r\n          <RadioGroup value={horizontal} onChange={handleHorizontalChange}>\r\n            <FormControlLabel value='right' control={<Radio />} label='Right' />\r\n            <FormControlLabel value='left' control={<Radio />} label='Left' />\r\n          </RadioGroup>\r\n        </FormControl>\r\n      </div>\r\n      <div className={classes.row}>\r\n        <Badge\r\n          color='secondary'\r\n          variant='dot'\r\n          badgeContent={1}\r\n          anchorOrigin={{\r\n            horizontal,\r\n            vertical,\r\n          }}\r\n          className={classes.margin}>\r\n          <MailIcon />\r\n        </Badge>\r\n        <Badge\r\n          color='secondary'\r\n          badgeContent={1}\r\n          anchorOrigin={{\r\n            horizontal,\r\n            vertical,\r\n          }}\r\n          className={classes.margin}>\r\n          <MailIcon />\r\n        </Badge>\r\n        <Badge\r\n          color='secondary'\r\n          badgeContent={12}\r\n          anchorOrigin={{\r\n            horizontal,\r\n            vertical,\r\n          }}\r\n          className={classes.margin}>\r\n          <MailIcon />\r\n        </Badge>\r\n        <Badge\r\n          color='secondary'\r\n          badgeContent={123}\r\n          anchorOrigin={{\r\n            horizontal,\r\n            vertical,\r\n          }}\r\n          className={classes.margin}>\r\n          <MailIcon />\r\n        </Badge>\r\n        <Badge\r\n          color='secondary'\r\n          max={999}\r\n          badgeContent={1337}\r\n          anchorOrigin={{\r\n            horizontal,\r\n            vertical,\r\n          }}\r\n          className={classes.margin}>\r\n          <MailIcon />\r\n        </Badge>\r\n      </div>\r\n    </div>\r\n  );\r\n}\r\n";